const singleLogin = 'sso/auth' //单点登录

export const APP_singleLogin = (param , _this) => {
	
	const reg = new RegExp("(^|&)code=([^&]*)(&|$)", "i");
	const r = window.location.search.substr(1).match(reg);
	if ( !r ){
		return new Promise((resolve, reject) => {/* executor函数 */
		    reject("请重新登录");
		})
	}else{
		param.type = 1
		param.threesidetoken = r[2]
		return _this.$http.post(singleLogin, param)
		.then(res => {
			const result = res.data || {}
			if(result.success){
				return result && result.data
			}else{
				reject({
					type:'noTurnUp',
					msg:result.msg || '操作错误'
				})
			}
		})
	}
	
	
}