const selectByRelationId = 'inParam/selectByRelationId' //取菜单的输入参数

export const common_inParam_selectByRelationId = (param , _this) => {
	return _this.$http.post(selectByRelationId, param)
	.then(res => {
		const result = res.data || {}
		return result && result.data
	})
	.catch(err => {
		uni.showModal({
			title:_this.$i18nMsg("lang-form-modalTipsError"),
			content:result.msg,
			showCancel:false,
			confirmText:_this.$i18nMsg("lang-form-modalConfirm")
		})
		return []
	})
}